@import url('variables.css');

h1.title{
    font-size: 36px;
    font-weight: var(--weight-semi-bold);
}
h2.title{
    font-size: 20px;
}

p.sm{
    font-size: 12px;
}

.form-cont--g{
    width: 100%;
    padding-top: var(--spc-12);
    padding-bottom: var(--spc-12);
    border-radius: var(--spc-8);
    box-shadow: 0px 0px 22px 10px rgba(0, 0, 0, 0.13);
}
.form-control--g{
    height: 2.5rem;
    width: 100%;
    border: var(--spc-0);
    border-radius: var(--spc-4);
    background: #E4E7FF;
    color: #6A6A6A;
    font-size: 11px;
    padding-left: var(--spc-16);
}
.form-cont--g .btn{
    width: 100%;
}

.btn{
    border-radius: 4px;
    padding: .6rem 1rem;
}
.btn-primary {
    font-size: 11px;
    background: var(--clr-primary);
    border-color: var(--clr-primary);
}
.btn-secondary {
    font-size: 11px;
    background: var(--clr-naturals-100);
    color: var(--clr-primary);
    border-color: var(--clr-primary);
}